import styles from "./daily.module.scss";
import { useState } from "react";

export const DailyPhrases = () => {
  const [marker, setMarker] = useState<number>(0);
  const [side, setSide] = useState<boolean>(true);
  const ph: { en: string; ru: string }[] = [
    {
      en: "I's not of you business",
      ru: "   ",
    },
    {
      en: "I mean it",
      ru: "  ",
    },
    {
      en: "I have no idea",
      ru: "   ",
    },
    {
      en: "I'm all ears",
      ru: "  - ",
    },
    { en: "You should go with us", ru: "    " },
    { en: "Will you call her?", ru: "  ?" },
    {
      en: "Find out",
      ru: "",
    },
    { en: "I don't care", ru: " " },
    { en: "It's up to you", ru: " " },
    {
      en: "What are you driving at?",
      ru: "   ?",
    },
    { en: "Give or take", ru: "  ()" },
    { en: "How come?", ru: "  ?" },
    {
      en: "Help yourself",
      ru: "",
    },
    { en: "Once & for all", ru: "  " },
    { en: "At once", ru: "" },
    { en: "Binge watch", ru: " " },
    {
      en: "Once in a while",
      ru: "  ",
    },
    {
      en: "Frankly speaking",
      ru: " ",
    },
    { en: "I'm sorry to bother you", ru: "  " },
    { en: "I'm positive", ru: "  " },
    {
      en: "Why don't we/you...?",
      ru: "    ...?",
    },
    { en: "I'm good to go!", ru: " " },
    { en: "I'm all ears", ru: "   " },
    { en: "I,m done!", ru: " /  !" },
  ];
  const incMarker = () => {
    setMarker(marker < ph.length - 1 ? marker + 1 : 0);
  };
  const decMarker = () => {
    setMarker(marker > 0 ? marker - 1 : ph.length - 1);
  };

  const changeSide = () => {
    setSide((val) => !val);
  };

  return (
    <main className={styles.wrapper}>
      <button onClick={decMarker}>-</button>
      <section className={side ? styles.rotate : ""} onClick={changeSide}>
        <div className={styles.one}>{ph[marker].en}</div>
        <div className={styles.two}>{ph[marker].ru}</div>
      </section>
      <button onClick={incMarker}>+</button>
    </main>
  );
};
